import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Despair () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>rules-sanity-despair</SubjectIdentifier>
      <SubjectKeyword>règle</SubjectKeyword>
      <SubjectTitle>Désespoir</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          Quand la volonté du personnage atteind zéro points il est alors face
          au désespoir le plus absolu, et doit alors verser dans la folie pour
          continuer à tenir face à une adversité contre laquelle il estime
          n'avoir aucune chance de survie.
        </p>
      </SubjectContent>
    </Subject>
  )
}
